body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  font-weight: 500;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  overflow-x: hidden;
  overflow: auto;
  background: #fff;
}

body {
  height: 100%;
  font-family: "DM Sans", sans-serif;

  font-weight: 400;

  font-style: normal;
  color: #8f8f8f;
  font-weight: 400;
  font-style: normal;
  font-size: 16px;
}

#root,
.container {
  width: 100%;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: #606060;
  border-bottom: 1px solid transparent;
}

.hilight {
  font-weight: bold;
}

.blue {
  color: #ffc63b;
}

a:hover {
  color: #9a9a9a;
}

h2 {
  font-family: "DM Serif Display", serif;
  font-weight: 400;
  width: auto;
  font-style: italic;
  display: inline-block;
  background: transparent;
  font-size: 50px;
  margin: 0.3em 0;
  margin-bottom: 0em;
  letter-spacing: -1.2px;
  color: #343945;
}

h3 {
  font-weight: 400;
  width: auto;
  display: inline-block;
  background: transparent;
  font-size: 16px;
  letter-spacing: -0.15px;

  margin: 0;
}

.product-name h3 {
  font-size: 30px;
  font-family: "DM Serif Display", sans-serif;
  padding: 0;
  font-weight: 400;
  font-style: italic;
  letter-spacing: 0.25px !important;
  color: #fff;
  margin-top: -62px;
  position: absolute;
}

.product-name p {
  display: inline-block;
  color: #444;
  margin-top: 5px;
  margin-bottom: 10px;
  letter-spacing: 0.5px;
}

h5 {
  font-weight: 400;
  width: auto;
  display: inline-block;
  z-index: 9999;
  position: relative;
  margin: 0px 0px;
}

p {
  letter-spacing: -0px;
  font-family: "DM Sans", sans-serif;
  font-size: 16px;
  color: #444;
  font-weight: 300;
  line-height: 150% !important;
}

.info {
  line-height: 200%;
}

::-webkit-scrollbar {
  width: 7px;
}

::-webkit-scrollbar-track {
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: #ccc;
}

.product-tile {
  display: inline-block;
  vertical-align: middle;
  height: 100%;
  box-sizing: border-box;
  margin-bottom: 75px;
  background: #fff;
  border-radius: 5px;
}

.product-tile:hover img {
  opacity: 0.75;
}

.product-tile p {
  font-size: 17px;
}
.product-tile img {
  transition: 0.3s;
  opacity: 1;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}

.link-arrow a {
  padding: 2.5px 0px;
  letter-spacing: 0.3px;
  display: inline-block;
  vertical-align: middle;
  border: 0px solid transparent;
  transition: 0.2s ease;
  padding-top: 0;
}

.hoverpointer {
  font-size: 14px;
  font-family: "DM Sans", sans-serif;
  letter-spacing: 0.3px;
  width: 149px;
  font-weight: 300;
  background: white;
  box-sizing: border-box;
  box-shadow: 5px 5px 90px rgba(0, 0, 0, 0.1);
  border-radius: 20px;
  padding: 15px 20px;
  opacity: 0;

  z-index: -99999999;
  position: relative;
  line-height: 15px;
  transition: 0.4s linear;
  margin-left: 0px;
  display: none;
}

.song {
  margin-top: -40px;
}

.product-name {
  box-sizing: border-box;
  text-align: left;
  position: absolute;
  padding: 2.5%;
  padding-left: 5%;
  padding-right: 5%;
  width: 100%;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  box-shadow: 12px 33px 50px -18px rgba(0, 0, 0, 0.15);
  box-sizing: border-box;
  transition: 0.3s;
}

.product-tile:hover {
  cursor: pointer;
}

.product-tile-back {
  position: absolute;
