.contact-box {
  width: 90%;
  margin: 0.75%;
  display: inline-block;
  padding: 2%;
  padding-bottom: 1%;
  padding-bottom: 1%;
  text-align: left;
  text-indent: 75px;
  text-transform: uppercase;
  letter-spacing: 1px;
  background: #f4f4f4;
  box-shadow: 4px 4px 6px rgba(0, 0, 0, 0.03);
  vertical-align: middle;
}

.contact-box p {
  font-size: 11px;
  letter-spacing: 0.5px;
  text-transform: lowercase;
}

.contact-box:hover {
  background: #fff;
  box-shadow: 0 0 0 white;
}

.home-button {
  background: linear-gradient(to right, #8694fd, #343945);
  border-radius: 40px;
  text-transform: uppercase;
  color: #fff;
  padding: 14px 48px;
  letter-spacing: 2.5px;
  font-weight: bold;
  font-size: 13px;
  line-height: 13px;
  box-shadow: 4px 4px 15px rgba(0, 0, 0, 0.15);
  border: 0px solid transparent;
}

.home-button:hover {
  color: #fff;
  background: #8694fd;
  border: 0px solid transparent !important;
  margin-top: 0px !important;
  box-shadow: 5.5px 5.5px 13px rgba(0, 0, 0, 0.15);
}

.home-button a:hover {
  border: 0px solid transparent !important;
}

.popup {
  top: 0;
  background: white;
