@-webkit-keyframes floatingAnimAlert{
  from{
    margin-top: 0;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);

  }
  to{
    margin-top: 1.2%;
  }
}

@keyframes floatingAnimAlert{
  from{
    margin-top: 0;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);

  }
  to{
    margin-top: 1.2%;
  }
}

@-webkit-keyframes floatingAnim{
  from{
    margin-top: 0;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);

  }
  to{
    margin-top: 2%;
    -webkit-transform: rotate(-5deg);
            transform: rotate(-5deg);
  }
}

@keyframes floatingAnim{
  from{
    margin-top: 0;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);

  }
  to{
    margin-top: 2%;
    -webkit-transform: rotate(-5deg);
            transform: rotate(-5deg);
  }
}

@-webkit-keyframes floatingAnimiPod{
  from{
    margin-top: 0;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);

  }
  to{
    margin-top: 2.4%;
    margin-left: .8%;
    -webkit-transform: rotate(-15deg);
            transform: rotate(-15deg);
  }
}

@keyframes floatingAnimiPod{
